<template>
    <div class="login-main">
        <div class="login-box">
            <div class="login-area">
                <div class="header-area">
                    <h1 v-if="showLogin" style="padding: 10px; padding-bottom: 2rem; letter-spacing: 5px;">WELCOME BACK</h1>
                    <h1 v-else style="padding: 10px; padding-bottom: 2rem; letter-spacing: 2px; font-size: 1.7rem;">WELCOME NEW USER</h1>
                    <div v-if="showLogin" class="google-login" style="display: flex; justify-content: center; padding-bottom: 1rem;">
                        <button class="google-button" @click="signInWithGoogle">
                            <i class="fab fa-google"></i>
                            <span>Log in with Google</span>
                        </button>
                    </div>
                </div>
                <div class="between-area" style="display: flex; align-items: center; justify-content: center; text-align: center; padding: 10px; padding-bottom: 2rem;">
                    <h6 v-if="showLogin">Or Log-in with your credentials</h6>
                    <h6 v-else>Insert your new credentials</h6>
                </div>
                <div class="credential-area">
                    <div class="user-input" style="display: flex; flex-direction: column; justify-content: space-around;">
                        <div class="login-inputs" style="display: flex; flex-direction: column; justify-content: space-around" v-if="showLogin">
                            <input type="email" placeholder="E-mail..." v-model="email">
                            <input type="password" placeholder="Password..." v-model="password">
                        </div>
                        <div class="signup-inputs" style="display: flex; flex-direction: column; justify-content: space-around;" v-else>
                            <input type="email" name="" placeholder="E-mail..." v-model="email">
                            <input type="password" name="" placeholder="Password..." v-model="password">
                            <input type="password" name="" placeholder="Confirm Password...">
                        </div>
                        <div class="checkbox-area" style="padding: 1rem;">
                            <div v-if="showLogin" class="checkbox" style="display: flex; flex-direction: row; align-items: center;">
                                <input type="checkbox" name="" id="">
                                <span>Stay Connected</span>
                            </div>
                            <div v-if="showLogin" class="remember-password">
                                <span>Forget Password</span>
                            </div>
                            <div v-else class="signup-checkbox" style="display: flex; flex-direction: row; align-items: center;">
                                <!-- <input type="checkbox" name="" id=""> -->
                                <span style="width: 16rem;">We're glad to see you</span>
                            </div>
                        </div>
                        <span style="padding-bottom: 20px; color: #802a2a; font-weight: bold;"  v-if="errMsg">{{ errMsg }}</span>
                        <button class="login" style="">
                            <span v-if="showLogin" style="flex: 1;" @click="login">Login</span>
                            <span v-else style="flex: 1;" @click="register">Signup</span>
                            <i class="fas fa-right-long" style="margin-left: auto;"></i>
                        </button>
                        <div class="sign-up" style="padding-top: 30px;" @click="changeLogin">
                            <span v-if="showLogin">Don't have an account yet? Sign up!</span>
                            <span v-else>Return to login</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { ref } from 'vue'
import { 
    getAuth, 
    createUserWithEmailAndPassword, 
    signInWithEmailAndPassword,
    GoogleAuthProvider, 
    signInWithPopup
 } from 'firebase/auth'
import { useRouter } from 'vue-router';


let showLogin = ref(true)

const changeLogin=()=>{
    showLogin.value=!showLogin.value
}

const email = ref("")
const password = ref("")
const router = useRouter()
const errMsg = ref()

const register = () => {
    createUserWithEmailAndPassword(getAuth(), email.value, password.value)
        .then(() => {
            console.log('Succesfully registered!')
            router.push('/firebase-portfoil/main')
        })
        .catch((error) => {
            console.log(error.code)
            alert(error.message)
        })
}
const login = () => {
    signInWithEmailAndPassword(getAuth(), email.value, password.value)
        .then(() => {
            console.log('Succesfully Logged-in')
            router.push('/firebase-portfoil/main')
        })
        .catch((error) => {
            console.log(error.code)
            switch (error.code) {
                case "auth/invalid-email":
                    errMsg.value="Wrong Credentials."
                    break;
                case "auth/user-not-found":
                    errMsg.value="Wrong Credentials."
                    break;
                case "auth/wrong-password":
                    errMsg.value="Wrong Credentials."
                    break
                default:
                    errMsg.value="Wrong Credentials."
                    break
            }
        })
}

const signInWithGoogle = () => {
    const provider = new GoogleAuthProvider()
    signInWithPopup(getAuth(), provider)
        .then((result) => {
            console.log(result.user)
            router.push('/firebase-portfoil/main')
        })
        .catch((error) => {
            console.error(error)
        })
}

</script>
<style scope>

    * {
        overflow: hidden;
    }

    .checkbox-area {
        display: flex; 
        flex-direction: row; 
        justify-content: space-around; 
        font-size: 0.8rem;
        text-align: center; 
        align-items: center; 
        align-content: center; 
        padding: 10px;
        gap: 20px;
    }

    .login-area, .signin-area {
        display: flex;
        flex-direction: column;
        padding: 100px;
        /* background-color: #f1f1f1; */
        height: auto;
        border-radius: 10px;
    }

    .login {
        display: flex; 
        flex-direction: row; 
        justify-content: space-between; 
        align-items: center; 
        background-color: #333652; 
        color: #fff; 
        padding: 10px; 
        border-radius: 5px;
        outline: none;
        border: 1px solid #333652;
        transition: ease-in 0.2s;
    }

    .login:hover {
        cursor: pointer;
        background-color: #fff;
        color: #333652;
    }

    .user-input input {
        margin: 5px;
        padding: 10px;
        outline: none;
        border: 1px solid #b7b7b7;
    }

    .google-button {
        display: flex;
        flex-direction: row;
        gap: 10px;
        padding: 10px;
        padding-left: 30px;
        padding-right: 30px;
        border: none;
        outline: none;
        box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
        background-color: #fff;
        transition: ease-in-out 0.3s;
    }

    .google-button:hover {
        cursor: pointer;
        background-color: #333652;
        color: #fff;
    }

    .login-main {
        display: flex;
        flex-direction: row;
        padding: 10px;
        justify-content: center;
        height: 100% !important;
        align-items: center;
    }

    .login-main {
        --s: 25vmin;
        --p: calc(var(--s) / 2);
        --c1: #56a7ee;
        --c2: #333652;
        --c3: #ffffff;
        --bg: var(--c3);
        --d: 4000ms;
        --e: cubic-bezier(0.76, 0, 0.24, 1);
        
        background-color: var(--bg);
        background-image:
            linear-gradient(45deg, var(--c1) 25%, transparent 25%),
            linear-gradient(-45deg, var(--c1) 25%, transparent 25%),
            linear-gradient(45deg, transparent 75%, var(--c2) 75%),
            linear-gradient(-45deg, transparent 75%, var(--c2) 75%);
        background-size: var(--s) var(--s);
        background-position: 
            calc(var(--p) *  1) calc(var(--p) *  0), 
            calc(var(--p) * -1) calc(var(--p) *  1), 
            calc(var(--p) *  1) calc(var(--p) * -1), 
            calc(var(--p) * -1) calc(var(--p) *  0);
        animation: 
            color var(--d) var(--e) infinite,
            position var(--d) var(--e) infinite;
        }

        @keyframes color {
        0%, 25% {
            --bg: var(--c3);
        }
        26%, 50% {
            --bg: var(--c1);
        }
        51%, 75% {
            --bg: var(--c3);
        }
        76%, 100% {
            --bg: var(--c2);
        }
        }

        @keyframes position {
        0% {
            background-position: 
            calc(var(--p) *  1) calc(var(--p) *  0), 
            calc(var(--p) * -1) calc(var(--p) *  1), 
            calc(var(--p) *  1) calc(var(--p) * -1), 
            calc(var(--p) * -1) calc(var(--p) *  0);
        }
        25% {
            background-position: 
            calc(var(--p) *  1) calc(var(--p) *  4), 
            calc(var(--p) * -1) calc(var(--p) *  5), 
            calc(var(--p) *  1) calc(var(--p) *  3), 
            calc(var(--p) * -1) calc(var(--p) *  4);
        }
        50% {
            background-position: 
            calc(var(--p) *  3) calc(var(--p) * 8), 
            calc(var(--p) * -3) calc(var(--p) * 9), 
            calc(var(--p) *  2) calc(var(--p) * 7), 
            calc(var(--p) * -2) calc(var(--p) * 8);
        }
        75% {
            background-position: 
            calc(var(--p) *  3) calc(var(--p) * 12), 
            calc(var(--p) * -3) calc(var(--p) * 13), 
            calc(var(--p) *  2) calc(var(--p) * 11), 
            calc(var(--p) * -2) calc(var(--p) * 12);
        }
        100% {    
            background-position: 
            calc(var(--p) *  5) calc(var(--p) * 16), 
            calc(var(--p) * -5) calc(var(--p) * 17), 
            calc(var(--p) *  5) calc(var(--p) * 15), 
            calc(var(--p) * -5) calc(var(--p) * 16);
        }
        }

        @media (prefers-reduced-motion) {
        .login-main {
            animation: none;
        }
        }


    .login-box {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        align-content: center;
        justify-content: center;
        background: rgba(255, 255, 255, 0.913);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(8.9px);
        -webkit-backdrop-filter: blur(8.9px);
        border-radius: 10px;
        height: auto;
        width: auto;
        padding: 20px;
        box-shadow: -7px 13px 28px 0px rgba(0,0,0,0.1);
    }

    @media (max-width: 768px) {

        .login-box {
            width: 90%;
            padding: 10px;
        }

        .login-area, .signin-area {
            padding: 20px;
        }

        .login {
            padding: 8px;
        }

        .user-input input {
            margin: 3px;
            padding: 8px;
        }

        .google-button {
            padding: 8px 20px;
        }

        .checkbox-area {
            font-size: 0.7rem;
            gap: 10px;
        }

    }

</style>